import React, {useEffect, useState} from 'react';
import style from './filter.module.scss';
import CloseIcon from '@mui/icons-material/Close';

const Filter = (props) => {
	const [filters, setFilters] = useState({
		product: '',
		brand: '',
		price: '',
	});

	function refreshFilters () {
		setFilters({
			product: '',
			brand: '',
			price: '',
		});
	}

	const [activeField, setActiveField] = useState('');

	const handleInputChange = (event) => {
		const {name, value} = event.target;

		//   price,   ,   
		if (name === 'price') {
			const filteredValue = value.replace(/[^0-9]/g, ''); //   
			setFilters((prevFilters) => ({...prevFilters, [name]: filteredValue}));
		} else {
			setFilters((prevFilters) => ({...prevFilters, [name]: value}));
		}

		setActiveField(name);
	};
	const handleKeyPress = (event) => {
		if (event.key === 'Enter') {
			handleFilterSubmit();
		}
	};

	const handleFilterSubmit = () => {
		//    
		const filteredValues = Object.fromEntries(
			Object.entries(filters).filter(([key, value]) => value !== '')
		);

		//   price   
		if (filteredValues.price) {
			filteredValues.price = parseFloat(filteredValues.price);
		}

		// ,       
		if (Object.keys(filteredValues).length > 0) {
			props.onFilter(filteredValues);
			refreshFilters();
		}

		// props.onFilter(filters);
	};



	return (
		<div className={style.container}>
			<div className={style.buttonContainer}>
				<button className={style.button} onClick={props.onToggle}>
					<CloseIcon/>
				</button>
			</div>
			<div className={style.content}>
				<div className={style.contentInput}>
					<label htmlFor="product">
						
						<input
							onKeyDown={handleKeyPress}
							name={'product'}
							placeholder={'  '}
							value={activeField === 'product' ? filters.product : ''}
							onChange={handleInputChange}
						/>
					</label>
				</div>
				<div className={style.contentInput}>
					<label htmlFor="brand">
						
						<input
							onKeyDown={handleKeyPress}
							name={'brand'}
							placeholder={'  '}
							value={activeField === 'brand' ? filters.brand : ''}
							onChange={handleInputChange}
						/>
					</label>
				</div>
				<div className={style.contentInput}>
					<label htmlFor="price">
						
						<input
							onKeyDown={handleKeyPress}
							name={'price'}
							placeholder={'  '}
							value={activeField === 'price' ? filters.price : ''}
							onChange={handleInputChange}
						/>
					</label>
				</div>
				<button
					className={style.submitButton}
					onClick={handleFilterSubmit}
					disabled={!!props.isLoading || props.isFirstRender === true}
				>
					
				</button>
			</div>
		</div>
	);
};

export default Filter;
